
.ratingselect .ratings-header {
  margin: 18px;
}
.ratingselect .ratings-header .title {
  margin-bottom: 12px;
  font-size: 14px;
  color: #07111b;
}
.ratingselect .ratings-header .rating-type .block {
  display: inline-block;
  padding: 8px 12px;
  font-size: 12px;
  line-height: 16px;
  margin-right: 8px;
  color: #4d555d;
}
.ratingselect .ratings-header .rating-type .block.active {
  color: #fff;
}
.ratingselect .ratings-header .rating-type .all {
  background-color: rgba(0,160,220,0.2);
}
.ratingselect .ratings-header .rating-type .all.active {
  background-color: #00a0dc;
}
.ratingselect .ratings-header .rating-type .positive {
  background-color: rgba(0,160,220,0.2);
}
.ratingselect .ratings-header .rating-type .positive.active {
  background-color: #00a0dc;
}
.ratingselect .ratings-header .rating-type .negative {
  background-color: rgba(77,85,93,0.2);
}
.ratingselect .ratings-header .rating-type .negative.active {
  background-color: #4d555d;
}
.ratingselect .switch {
  padding: 12px 0px;
  margin: 0px 18px;
  color: #93999f;
  line-height: 24px;
  position: relative;
}
.ratingselect .switch:before {
  display: block;
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.ratingselect .switch.only-content .icon-check_circle {
  color: #00c850;
}
.ratingselect .switch .span {
  font-size: 24px;
  vertical-align: top;
}
.ratingselect .switch .switch-content {
  font-size: 12px;
}


