
.seller {
  position: absolute;
  top: 176px;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
}
.seller .overview {
  padding: 18px;
  position: relative;
}
.seller .overview .title {
  margin-bottom: 8px;
  line-height: 14px;
  color: #07111b;
}
.seller .overview .desc {
  padding-bottom: 18px;
  position: relative;
  font-size: 0px;
}
.seller .overview .desc:after {
  display: block;
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.seller .overview .desc .star {
  display: inline-block;
  margin-right: 8px;
  vertical-align: top;
  line-height: 18px;
}
.seller .overview .desc .text {
  margin-right: 12px;
  display: inline-block;
  vertical-align: top;
  font-size: 10px;
  color: #4d555d;
  line-height: 18px;
}
.seller .overview .favorite {
  position: absolute;
  top: 18px;
  right: 18px;
  text-align: center;
  font-size: 0px;
  width: 50px;
  height: 38px;
}
.seller .overview .favorite .icon-favorite {
  color: #93999f;
  font-size: 24px;
  line-height: 24px;
}
.seller .overview .favorite .text {
  color: #93999f;
  font-size: 10px;
  line-height: 10px;
  margin-top: 4px;
}
.seller .overview .favorite.active .icon-favorite {
  color: #f01414;
}
.seller .overview .remark {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 18px;
}
.seller .overview .remark .block {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  border-right: 1px solid rgba(7,17,27,0.1);
}
.seller .overview .remark .block:last-child {
  border: 0;
}
.seller .overview .remark .block h2 {
  margin-bottom: 4px;
  line-height: 10px;
  font-size: 10px;
  color: #93999f;
}
.seller .overview .remark .block .content {
  line-height: 24px;
  font-size: 10px;
  color: #07111b;
}
.seller .overview .remark .block .content .stress {
  font-size: 24px;
}
.seller .bulletin {
  padding: 18px 18px 0 18px;
}
.seller .bulletin .title {
  margin-bottom: 8px;
  line-height: 14px;
  color: #07111b;
}
.seller .bulletin .content-wrapper {
  padding: 0 12px 16px 12px;
  position: relative;
}
.seller .bulletin .content-wrapper:after {
  display: block;
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.seller .bulletin .content-wrapper .content {
  line-height: 24px;
  font-size: 12px;
  color: #f01414;
}
.seller .pics {
  padding: 18px;
}
.seller .pics .title {
  margin-bottom: 12px;
  line-height: 14px;
  color: #07111b;
  font-size: 14px;
}
.seller .pics .pic-wrapper {
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
}
.seller .pics .pic-wrapper .pic-list {
  font-size: 0;
}
.seller .pics .pic-wrapper .pic-list .pic-item {
  display: inline-block;
  margin-right: 6px;
  width: 120px;
  height: 90px;
}
.seller .pics .pic-wrapper .pic-list .pic-item:last-child {
  margin: 0;
}
.seller .info {
  padding: 18px 18px 0 18px;
  color: #07111b;
}
.seller .info .title {
  padding-bottom: 12px;
  line-height: 14px;
  position: relative;
  color: #07111b;
  font-size: 14px;
}
.seller .info .title:after {
  display: block;
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.seller .info .info-item {
  padding: 16px 12px;
  line-height: 16px;
  position: relative;
}
.seller .info .info-item:after {
  display: block;
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.seller .info .info-item:last-child:after {
  border: 0;
}


