
.food {
  position: fixed;
  top: 0;
  bottom: 48px;
  width: 100%;
  z-index: 30;
  background-color: #fff;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.food.move-enter-active,
.food.move-leave-active {
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.food.move-enter,
.food.move-leave-active {
  -webkit-transform: translate3d(0, 100%, 0);
          transform: translate3d(0, 100%, 0);
}
.food .img-header {
  position: relative;
  height: 0;
  padding-top: 100%;
}
.food .img-header img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
}
.food .img-header .back {
  position: absolute;
  top: 10px;
  left: 0;
}
.food .img-header .back i {
  display: block;
  padding: 10px;
  font-size: 20px;
  color: #fff;
}
.food .content {
  position: relative;
  padding: 18px;
}
.food .content .title {
  line-height: 14px;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
}
.food .content .detail {
  margin-bottom: 18px;
  font-size: 0px;
  line-height: 10px;
}
.food .content .detail .sell-count,
.food .content .detail .rating {
  font-size: 10px;
  color: #9d999f;
}
.food .content .detail .rating {
  margin-left: 12px;
}
.food .content .price {
  font-weight: 700;
  line-height: 24px;
}
.food .content .price .now {
  font-size: 14px;
  color: #f01414;
  font-weight: 700;
  line-height: 48px;
}
.food .content .price .food-old-price {
  font-size: 10px;
  color: #93999f;
  text-decoration: line-through;
}
.food .content .carcontrol-wrapper {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.food .content .buy {
  position: absolute;
  right: 18px;
  height: 18px;
  bottom: 18px;
  z-index: 10;
  height: 24px;
  line-height: 24px;
  padding: 0 12px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 10px;
  border-radius: 12px;
  color: #fff;
  background-color: #00a0dc;
}
.food .content .buy.fade-enter-active,
.food .content .buy.fade-leave-active {
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}
.food .content .buy.fade-enter,
.food .content .buy.fade-leave-active {
  opacity: 0;
}
.food .info {
  padding: 18px;
}
.food .info .title {
  line-height: 14px;
  margin-bottom: 6px;
  font-size: 14px;
  color: #07111b;
}
.food .info .text {
  padding: 0 8px;
  font-size: 12px;
  line-height: 24px;
  color: #4d555d;
}
.food .ratings-wrapper .ratings-list .rating-item {
  padding: 16px 18px;
  position: relative;
}
.food .ratings-wrapper .ratings-list .rating-item:before {
  display: block;
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.2);
  content: ' ';
}
.food .ratings-wrapper .ratings-list .rating-item .user {
  position: absolute;
  right: 16px;
  line-height: 12px;
  font-size: 0px;
}
.food .ratings-wrapper .ratings-list .rating-item .user .user-name {
  display: inline-block;
  vertical-align: top;
  font-size: 10px;
  color: #93999f;
}
.food .ratings-wrapper .ratings-list .rating-item .user .avatar {
  margin-left: 6px;
  border-radius: 50%;
}
.food .ratings-wrapper .ratings-list .rating-item .time {
  margin-bottom: 6px;
  line-height: 12px;
  font-size: 10px;
  color: #93999f;
}
.food .ratings-wrapper .ratings-list .rating-item .text {
  line-height: 16px;
  font-size: 12px;
  color: #07111b;
}
.food .ratings-wrapper .ratings-list .rating-item .text .icon-thumb_up,
.food .ratings-wrapper .ratings-list .rating-item .text .icon-thumb_down {
  margin-right: 4px;
  line-height: 24px;
  font-size: 12px;
}
.food .ratings-wrapper .ratings-list .rating-item .text .icon-thumb_up {
  color: #00a0dc;
}
.food .ratings-wrapper .ratings-list .rating-item .text .icon-thumb_down {
  color: #93999f;
}
.food .ratings-wrapper .no-ratings {
  padding: 16px 0px;
  font-size: 12px;
  color: #93999f;
}


