.supports .support-item:after {
  display: block;
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.supports .support-item:last-child:after {
